<template>
    <div>
    <div>
      <div class="ui form" :class="{loading: loading, success: successActive, error: errorActive}">
      <div class="field">
      <slot>
      <label class="ui label">Post {{field}}</label>
      </slot>
      <div class="ui action input">
        <input type="text" v-model="inputText" @keyup.enter="processInput" :placeholder="placeholderText">
        <button @click="processInput" class="ui button" :class="buttonClass">Post</button>
      </div>
      </div>
      <div class="ui success message">
        <i @click="successActive = !successActive" class="close icon"></i>
        <div class="header">
          Your post was successful. Click <button class="ui icon button"><i class="refresh icon" @click="refreshTable"></i></button> to refresh.
        </div>
      </div>
      <div class="ui error message">
        <i @click="errorActive = !errorActive" class="close icon"></i>
        <div class="header">
          There was an error in posting.
        </div>
      </div>
    </div>
  </div>
</div>
</template>

<script>
import {mapState} from 'vuex'
import axios from 'axios'

export default {
  data () {
    return {
      loading: false,
      inputText: '',
      successActive: false,
      errorActive: false,
      placeholderText: 'Ask a question.',
      field: 'Question',
      buttonClass: ''
    }
  },
  props: ['space','question','buttonClass'],
  computed: mapState(['loaderColor','loaderSize','currentUser']),
  mounted: function() {
    if(this.question) {
      this.placeholderText = 'Enter an answer. '
      this.field = 'Answer'
    }
  },
  methods: {
    processInput() {
      let email = 'inquire@sparkbot.io'
      let mdMessage = `<@personEmail:${email}> `
      if(this.question) {
        mdMessage += 'answer ' + this.question.sequence + ' ' + this.inputText
        this.postQuestion(mdMessage)
      }
      else {
        mdMessage += this.inputText
        this.postQuestion(mdMessage)
      }
    },
    postQuestion (mdMessage) {
      this.loading = true
      let url = 'https://api.ciscospark.com/v1/messages'
      let config = {
             headers: {'Authorization': "Bearer " + this.currentUser.token}
        };
      let body = {
        roomId: this.space,
        markdown: mdMessage
      }
      axios.post(url,body,config).then(response => {
        console.log('Posted to Spark Room')
        this.loading = false
        this.successActive = true
        this.inputText = ''
      })
      .catch(err => {
        console.error(err)
        this.loading = false
        this.errorActive = true
        this.inputText = ''
      })
    },
    refreshTable() {
      this.$events.fire('filter-reset')
    }
  }
}
</script>

<style>

</style>



