<template>
  <div class="ui text container">
  <div class="ui segment">
    <div class="ui inverted dimmer" :class="{active: loading}">
      <div class="ui large text loader">Loading</div>
      <p></p>
      <p></p>
      <p></p>
    </div>
  <div>
    <div class="ui top attached block header segment">
    <h1 class="ui header center aligned">Available FAQ Spaces</h1>
    </div>
    <div class="ui bottom attached segment very padded">
    <div class="ui relaxed divided massive list left aligned">
      <div class="item" v-for="space in userSpaces">
        <i class="comment outline icon"></i>
          <div class="content">
            <router-link v-bind:to="'/space/' + space._id">{{space.displayName}}</router-link>
          </div>
      </div>
    </div>
  </div>
  </div>
</div>
  </div>
</template>

<script>
import {mapState, mapActions} from 'vuex'

export default {
  data () {
    return {
      loading: true
    }
  },
  computed: mapState(['isAuthenticated','userSpaces','loaderColor','loaderSize']),
  methods: mapActions(['loadSpaces']),
  mounted: function () {
      this.loadSpaces()
    },
  watch: {
    userSpaces: function( array ) {
      if(array.length >0)
        this.loading = false
    }
  }
}
</script>

<style>

</style>



