<template>
  <div class="ui form">
  <div class="field">
  <label class="ui label">Search</label>
  <div class="ui action input">
      <input class="prompt" type="text" v-model="filterText" @keyup.enter="doFilter" placeholder="Search questions">
    <button class="ui primary button" @click="doFilter">Search</button>
    <button class="ui button" @click="resetFilter">Reset</button>
  </div>
  </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      filterText: ''
    }
  },
  methods: {
    doFilter () {
      this.$events.fire('filter-set', this.filterText)
    },
    resetFilter () {
      this.filterText = ''  // clear the text in text input
      this.$events.fire('filter-reset')
    }
  }
}
</script>



